er style on focus */\n  border-right: 0;\n  transition: border 0.3s;\n}\n\nbutton.search_location-btn {\n  border: 3px solid gray; /* Set the initial border style for the button */\n  border-left: none;\n  background-color: rgb(109, 155, 255);\n  outline: none; /* Remove the default focus outline */\n}\n\ninput:focus + button.search_location-btn {\n  border: 3px solid black; /* Change the border style for the button on input focus */\n  border-left: 0;\n  transition: border 0.3s;\n}\n\n.toggle_metric {\n  grid-area: metric;\n  display: flex;\n  justify-content: end;\n  align-items: center;\n}\n.toggle_metric button {\n  background-color: blue;\n  color: white;\n  width: 50%;\n  max-width: 100px;\n  height: 100%;\n  max-height: 50px;\n  margin-right: 20px;\n  font-family: inherit;\n  font-size: large;\n  border-radius: 20px;\n}\n.toggle_metric button.active {\n  /* border: 4px solid rgb(219, 52, 52); */\n  border: none;\n  outline: 4px solid rgb(0, 0, 0);\n}\n.toggle_metric button:last-child {\n  margin-right: 0;\n}\n.titleArea {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n.titleArea h1,\n.titleArea h4 {\n  margin: 10px 0;\n}\n.area_head {\n  font-size: 2em;\n  text-align: center;\n}\n.date_time {\n  font-size: large;\n  text-align: center;\n}\n.location_dropdown {\n  min-width: 15em;\n  position: absolute;\n  margin: 2em 0;\n  top: 10px;\n  left: 0;\n  width: 100%;\n  z-index: 2;\n  display: none;\n}\n.location_dropdown * {\n  box-sizing: border-box;\n}\n.location_menu {\n  list-style: none;\n  padding: 0em 0.5em;\n  background: rgb(3 126 134 / 90%);\n  box-shadow: 0 0.5em 1em rgba(0, 0, 0, 0.2);\n  color: rgb(0, 0, 0);\n  border-radius: 0.5em;\n  width: 100%;\n  /* Change to 15em on click */\n  height: 0;\n  overflow-y: scroll;\n}\n.location_menu::-webkit-scrollbar {\n  width: 8px;\n}\n.location_menu::-webkit-scrollbar-track {\n  background-color: #40404084;\n}\n.location_menu::-webkit-scrollbar-thumb {\n  background-color: #39678f;\n}\n.location_menu::-webkit-scrollbar-thumb:hover {\n  background: #9e9e9e;\n}\n.location_menu li {\n  padding: 0.7em;\n  margin: 0.3em 0;\n  border-radius: 0.5em;\n  cursor: pointer;\n}\n.location_menu li:hover {\n  background-color: #242424;\n  color: white;\n}\n.active_menu {\n  background-color: #1e1e1e;\n  color: white;\n}\n.empty_location_List {\n  text-align: center;\n  width: 100%;\n  height: 100%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n.text-fade-in {\n  animation: textFade 300ms ease-out;\n}\n.menu_open {\n  border: 1px #383838;\n  height: 15em;\n}\n/* HTML: <div class=\"loader\"></div> */\n/* HTML: <div class=\"loader\"></div> */\n.loader {\n  width: 50px;\n  aspect-ratio: 1;\n  border-radius: 50%;\n  border: 8px solid lightblue;\n  border-right-color: rgb(9, 53, 85);\n  animation: l2 1s infinite linear;\n}\n.weather-forecast {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n\n.forecast-day {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 10px;\n  border-radius: 5px;\n  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);\n}\n\n.date-day {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n}\n.forcast-heading {\n  margin-left: 1em;\n  font-family: inherit;\n  font-size: 1.25rem;\n  font-weight: 600;\n}\n.temperature {\n  flex: 1;\n  display: flex;\n  justify-content: flex-start;\n  align-items: center;\n}\n\n.description,\n.night-description {\n  flex: 2;\n  text-align: left;\n  margin-left: 4.5em;\n}\n\n.rain-chance {\n  flex: 1;\n  text-align: right;\n}\n.rain-chance svg {\n  height: 12px;\n  margin-right: 4px;\n  width: 12px;\n}\n.high {\n  font-size: 1.5rem;\n  font-weight: 600;\n}\n@keyframes l2 {\n  to {\n    transform: rotate(1turn);\n  }\n}\n.change_Display_Menu {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n.hide_Menu {\n  display: none;\n}\n.show_Menu {\n  display: block;\n}\n.weatherForcastArea {\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  grid-gap: 10px;\n  grid-template-areas: \"weather-1 weather-2\";\n  padding: 0 2rem;\n}\n.tempDescription {\n  text-transform: capitalize;\n}\n.weather-area {\n  display: grid;\n  grid-template-columns: 1fr 1.25fr;\n  grid-gap: 10px;\n}\n.temp-weather-area {\n  display: flex;\n  flex-direction: column;\n  align-items: start;\n  justify-content: start;\n  max-height: 90%;\n}\n.temp-descrip-area {\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n}\n.weather_Info {\n  display: flex;\n  align-items: center;\n}\n.weather_Info img {\n  width: 100%;\n  height: 100%;\n  max-width: 50px;\n  max-height: 50px;\n  object-fit: cover;\n}\n.current-temp {\n  display: flex;\n  flex-direction: column;\n  align-items: start;\n  justify-content: center;\n  min-height: 50%;\n}\n.current-temp .temp {\n  font-size: 4em;\n  font-weight: 500;\n}\n.current-temp .description {\n  font-size: 1.5em;\n  font-weight: 400;\n}\n.temp-text {\n  font-size: 3.5rem;\n  font-weight: 600;\n}\n.tempDescription {\n  font-size: 1.75rem;\n  font-weight: 600;\n}\n.temp-weather-area h3 {\n  font-size: 1.5rem;\n  font-weight: 600;\n}\n.icon-area {\n  min-width: 150px;\n  max-width: 50%;\n  min-height: 150px;\n  max-height: 50%;\n}\n.info-box {\n  margin-left: 5px;\n}\n.info-box h3 {\n  font-size: 1rem;\n  font-weight: 600;\n}\n.uv-index-info {\n  width: 35px;\n  height: 35px;\n  border-radius: 50%;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 1.25rem;\n  font-weight: 600;\n  background-color: rgb(123, 123, 123);\n}\n\n@keyframes l3 {\n  to {\n    transform: rotate(1turn);\n  }\n}\n@keyframes l4 {\n  to {\n    transform: rotate(1turn);\n  }\n}\n@keyframes textFade {\n  0% {\n    transform: translateX(-1em);\n    opacity: 0;\n  }\n  100% {\n    transform: translateX(0);\n    opacity: 1;\n  }\n}\n@media screen and (max-width: 600px) {\n  .area_head {\n    font-size: 1.5em;\n  }\n  .weather-area {\n    display: block;\n  }\n  .weatherForcastArea {\n    display: block;\n  }\n  .temp-weather-area {\n    align-items: center;\n    justify-content: center;\n    line-height: 10px;\n  }\n  .current-temp {\n    align-items: center;\n  }\n  .description {\n    display: none;\n  }\n  header {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n  }\n  .toggle_metric {\n    width: 100%;\n    margin-top: 15px;\n    justify-content: center;\n    align-items: end;\n  }\n  .toggle_metric button {\n    min-height: 35px;\n  }\n}\n"],"sourceRoot":""}]);
